import React, { Component } from 'react'
import { Link } from "react-router-dom";
import axios from 'axios';
const getProductDetailData = () => axios.get('/ds_detail_FoodZone')
    .then((res) => res.data);
export default class FoodZoneDetail extends Component {
    constructor(props) {
        super(props)
        this.state = {
            data: [],
            numberInit: 1,
            numberEnd: 8,
            flag: ['true', 'false', 'false', 'false', 'false'],
            color: ['#C19D60', '', '', '', '']
        }
    }
    componentDidMount() {
        getProductDetailData().then((res) => {
            console.log(res.recordset);
            this.setState({
                data: res.recordset
            })

        })
    }
    handleClick1 = () => {
        if (this.state.flag[0] !== true) {
            this.setState({
                color: ['#C19D60', '', '', '', ''],
                flag: [true, false, false, false, false],
                numberInit: 1,
                numberEnd: 8
            })
        }
    }
    handleClick2 = () => {
        if (this.state.flag[1] !== true) {
            this.setState({
                color: ['', '#C19D60', '', '', ''],
                flag: [false, true, false, false, false],
                numberInit: 9,
                numberEnd: 13
            })
        }
    }
    handleClick3 = () => {
        if (this.state.flag[2] !== true) {
            this.setState({
                color: ['', '', '#C19D60', '', ''],
                flag: [false, false, true, false, false],
                numberInit: 14,
                numberEnd: 17
            })
        }
    }
    handleClick4 = () => {
        if (this.state.flag[3] !== true) {
            this.setState({
                color: ['', '', '', '#C19D60', ''],
                flag: [false, false, false, true, false],
                numberInit: 18,
                numberEnd: 21
            })
        }
    }
    handleClick5 = () => {
        if (this.state.flag[4] !== true) {
            this.setState({
                color: ['', '', '', '', '#C19D60'],
                flag: [false, false, false, false, true],
                numberInit: 22,
                numberEnd: 26
            })
        }
    }
    render() {
        return (
            <div>
                <header className="section-header food-zone" style={{ backgroundImage: 'url(http://restabook.kwst.net/dark/images/bg/12.jpg)' }}>
                    <div className="container food-container">
                        <nav className="navbar navbar-expand-lg navbar-light bg-light background-black">
                            <a className="navbar-brand" href>SANJALIKA FOOD</a>
                            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                                <span className="navbar-toggler-icon" />
                            </button>
                            <div className="collapse navbar-collapse" id="navbarNav">
                                <ul className="navbar-nav menu-right">
                                    <li className="nav-item active">
                                        <Link to="/" className="nav-link">HOME</Link>
                                    </li>
                                    <li className="nav-item">
                                        <Link to="" className="nav-link">NEWS</Link>
                                    </li>
                                    <li className="nav-item">
                                        <div className="menu-product">
                                            <Link to="" className="nav-link layer1">EXPERIENCE</Link>
                                            <div className="layer2">
                                                <ul>
                                                    <li><Link to="/DragonPark">DRAGON PARK</Link></li>
                                                    <li><Link to="/WaterPark">WATER PARK</Link></li>
                                                    <li><Link to="/QueenCableCars">QUEEN CABLE CARS &amp; THE MYSTERIOUS HILLS</Link></li>
                                                    <li><Link to="/LightHouse">SANJALIKA LIGHTHOUSE</Link></li>
                                                    <li><Link to="/Beach">SANJALIKA BEACH</Link></li>
                                                    <li><Link to="/Gallery">SANJALIKA GALLERRY</Link></li>
                                                    <li><Link to="/OldQuarterKiosks">SANJALIKA OLD QUARTER KIOSKS</Link></li>
                                                    <li><Link to="/FoodZone">FOOD ZONE</Link></li>
                                                </ul>
                                            </div>
                                        </div>
                                    </li>
                                    <li className="nav-item">
                                        <Link to="/AboutUs" className="nav-link">ABOUT US</Link>
                                    </li>
                                    <li className="nav-item">
                                        <Link to="/ContactUs" className="nav-link">CONTACT US</Link>
                                    </li>
                                    <li className="nav-item buy-ticket">
                                        <Link to="" className="nav-link">BUY TICKET</Link>
                                    </li>
                                </ul>
                            </div>
                        </nav>
                        <div className="header-banner text-center food-detail">
                            <h2 style={{ color: '#C19D60' }}><i>Special menu offers.</i></h2>
                            <h1>Discover Our Menu</h1>
                        </div>
                    </div>
                </header>
                <section className="main">
                    <div className="FoodZoneDetail-Main">
                        <div className="Menu-FoodZoneDetail-Main">
                            <ul>
                                <li style={{ color: this.state.color[0] }} onClick={() => this.handleClick1()}><span>01. </span>Main dishes</li>
                                <li style={{ color: this.state.color[1] }} onClick={() => this.handleClick2()}><span>02. </span>Starter</li>
                                <li style={{ color: this.state.color[2] }} onClick={() => this.handleClick3()}><span>03. </span>Desserts</li>
                                <li style={{ color: this.state.color[3] }} onClick={() => this.handleClick4()}><span>04. </span>Sea Food</li>
                                <li style={{ color: this.state.color[4] }} onClick={() => this.handleClick5()}><span>05. </span>Drinks</li>
                            </ul>
                        </div>
                        <div className="container mt-5">
                            <div className="menu-order">
                                <div className="row pt-4">
                                    {this.state.data.map((val, key) => {
                                        if (val.ID >= this.state.numberInit && val.ID <= this.state.numberEnd) {
                                            return (<div className="col-sm-6 pb-5" key={key}>
                                                <div className="row">
                                                    <div className="col-sm-2 menu-order-food-img">
                                                        <img src={val.Image} alt="" />
                                                    </div>
                                                    <div className="col-sm-8 menu-order-food-content pt-2">
                                                        <h3>{val.Title}</h3>
                                                        <p>{val.Content}</p>
                                                    </div>
                                                    <div className="col-sm-1 menu-order-food-price pt-1">
                                                        ${val.Price}
                                                    </div>
                                                </div>
                                            </div>)
                                        }
                                        else {
                                            return ''
                                        }
                                    })}
                                </div>
                            </div>
                        </div>
                    </div>
                </section>

                <div className="food-footer">
                    <div className="container menu-food-footer">
                        <ul className="inner-footer">
                            <li><Link to="/">HOME</Link></li>
                            <li><Link to="/AboutUs">ABOUT US</Link></li>
                            <li><Link to="#">ACTIVITIES</Link></li>
                            <li><Link to="/ContactUs">CONTACT US</Link></li>
                            <li><Link to="/News">NEWS</Link></li>
                            <li><Link to="#">MAPS</Link></li>
                            <li><Link to="#">SUPPORT</Link></li>
                        </ul>
                        <h1 className="text-center">SANJALIKA WATER PARK</h1>
                        <div className="footer-three-icon">
                            <div className="home-three-icon">
                                <Link to="" className="icon-twitter"><i className="fab fa-twitter" /></Link>
                            </div>
                            <div className="home-three-icon">
                                <Link to="" className="icon-facebook"><i className="fab fa-facebook-f" /></Link>
                            </div>
                            <div className="home-three-icon">
                                <Link to="" className="icon-instagram"><i className="fab fa-instagram" /></Link>
                            </div>
                        </div>
                        <h4 className="text-center"><span style={{ color: 'white', fontWeight: 'bold' }}>SANJALIKA</span> © 2021 All Rights Reserved.</h4>
                    </div>
                </div>
            </div>
        )
    }
}
